body {
  min-width: 375px;
  letter-spacing: .02em;
  background-color: var(--background-lighter);
  font-family: Montserrat, sans-serif;
  font-size: 12px;
}

.block-scroll {
  width: 100%;
  position: fixed;
  top: 0;
  left: 0;
  overflow-y: scroll;
}

a {
  font-family: inherit;
  text-decoration: none;
}

p {
  margin: 0;
  padding: 0;
}

ul {
  margin: 0;
  padding-left: 0;
  list-style: none;
}

h1, h2, h3 {
  margin: 0;
}

img {
  height: auto;
  width: 100%;
  display: block;
}

button {
  cursor: pointer;
  font-family: inherit;
}

.visually-hidden {
  width: 1px;
  height: 1px;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  border: 0;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

.container {
  max-width: 100%;
  padding-left: 15px;
  padding-right: 15px;
}

@media screen and (min-width: 414px) {
  .container {
    width: 414px;
    margin-left: auto;
    margin-right: auto;
  }
}

@media screen and (min-width: 1024px) {
  .container {
    width: 1024px;
    padding-left: 47px;
    padding-right: 47px;
  }
}

@media screen and (min-width: 1280px) {
  .container {
    width: 1280px;
    padding-left: 85px;
    padding-right: 85px;
  }
}

:root {
  --logo-theme-dark: #fff;
  --logo-theme-light: #494848;
  --subtitle-color: #b4b4b4;
  --accent-color: #f3c653;
  --accent-color1: rgba(243, 198, 83, .7);
  --accent-color2: #e5e5e5;
  --accent-color3: #000;
  --primary-text-color: #5f5f5f;
  --secondary-text-color: #7a7a7a;
  --accent-text-color: #c79002;
  --logo-font: "Film P3";
  --main-font: "Montserrat";
  --additional-font: "Times New Roman";
  --background-lighter: #fffdee;
  --background-darker: #f0ecd6;
  --footer-background: #0e0c07;
  --btn-backgroubd: rgba(0, 0, 0, .4);
  --shadow-color: rgba(0, 0, 0, .25);
  --border-color1: #bababa;
  --transition-fast: 250ms cubic-bezier(.4, 0, .2, 1);
  --transition-slow: 500ms cubic-bezier(.4, 0, .2, 1);
}

.header {
  z-index: 100;
  width: 100%;
  -webkit-backdrop-filter: blur(5px);
  backdrop-filter: blur(5px);
  background-image: linear-gradient(#000 20%, rgba(0, 0, 0, .75) 40%, rgba(0, 0, 0, .5) 60%, rgba(0, 0, 0, .25) 80%, rgba(0, 0, 0, .1));
  padding-top: 16px;
  padding-bottom: 16px;
  position: fixed;
  top: 0;
  right: 0;
  box-shadow: 0 0 2px rgba(0, 0, 0, .035), 0 0 5px rgba(0, 0, 0, .047), 0 0 10px rgba(0, 0, 0, .06), 0 0 18px rgba(0, 0, 0, .07), 0 0 33px rgba(0, 0, 0, .086), 0 0 80px rgba(0, 0, 0, .12);
}

.header__container {
  justify-content: space-between;
  align-items: center;
  display: flex;
}

.header__logo {
  letter-spacing: .3em;
  color: var(--logo-theme-dark);
  transition: color var(--transition-fast);
  font-family: Film P3;
  font-size: 36px;
  font-weight: 700;
  line-height: 1.08333;
  text-decoration: none;
  display: block;
}

.header__logo:hover, .header__logo:focus {
  color: var(--accent-color);
}

.header__nav-list {
  align-items: center;
  gap: 20px;
  display: flex;
}

.header__nav-link {
  letter-spacing: .02em;
  color: var(--accent-color);
  transition: var(--transition-fast);
  align-self: center;
  font-family: Montserrat;
  font-size: 16px;
  font-weight: 500;
  line-height: 1.25;
}

.header__tel-icon {
  fill: var(--accent-color);
  transition: var(--transition-fast);
}

.header__nav-items {
  gap: 6px;
  display: flex;
}

.header__nav-tel {
  color: var(--logo-theme-dark);
  transition: var(--transition-fast);
  line-height: 0;
  display: flex;
}

.header__tel-number {
  transition: var(--transition-fast);
  align-self: center;
  display: none;
}

.header__social-list {
  display: none;
}

.header__social-link {
  fill: var(--accent-color);
  transition: var(--transition-slow);
}

.header__social-link:hover, .header__social-link:focus {
  fill: var(--accent-text-color);
}

.header__nav-link:hover, .header__nav-link:focus {
  color: var(--accent-text-color);
}

.header__nav-tel:hover .header__tel-icon, .header__nav-tel:focus .header__tel-icon {
  fill: var(--accent-text-color);
}

.header__nav-tel:hover .header__tel-number, .header__nav-tel:focus .header__tel-number {
  color: var(--accent-text-color);
}

@media screen and (max-width: 360px) {
  .header__logo {
    font-size: 32px;
  }

  .header__nav-link {
    font-size: 14px;
  }

  .header__nav-list {
    gap: 10px;
  }
}

@media screen and (min-width: 1024px) {
  .header {
    padding-top: 30px;
    padding-bottom: 30px;
  }

  .header__tel-number {
    display: block;
  }

  .header__nav-tel {
    letter-spacing: .02em;
    border: 1px solid rgba(243, 198, 83, .7);
    border-radius: 2px;
    padding: 8px;
    font-family: Montserrat;
    font-size: 14px;
    font-weight: 400;
    line-height: .823529;
  }

  .header__nav-items {
    gap: 32px;
  }

  .header__nav-list {
    gap: 72px;
  }

  .header__nav-link {
    font-size: 20px;
    line-height: 1.2;
  }
}

@media screen and (min-width: 1280px) {
  .header__social-list {
    gap: 20px;
    display: flex;
  }
}

@keyframes textFocus {
  0% {
    filter: blur(12px);
    opacity: 0;
  }

  100% {
    filter: blur();
    opacity: 1;
  }
}

@keyframes textFocusAdditional {
  0% {
    letter-spacing: -.5em;
    filter: blur(12px);
    opacity: 0;
    transform: translateZ(-800px);
  }

  100% {
    filter: blur();
    opacity: 1;
    transform: translateZ(0);
  }
}

@keyframes textFromBotton {
  0% {
    opacity: 0;
    transform: translateY(100px);
  }

  50% {
    opacity: .5;
  }

  100% {
    opacity: 1;
    transform: translateY(0);
  }
}

.hero {
  font-family: var(--main-font);
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  padding-top: 181px;
  padding-bottom: 20px;
}

.hero .container {
  position: relative;
}

.hero-title {
  text-align: center;
  font-family: var(--logo-font);
  letter-spacing: .3em;
  color: var(--logo-theme-dark);
  margin-bottom: 24px;
  margin-left: 28.8px;
  font-size: 96px;
  font-weight: 700;
  line-height: 1.07;
  animation-name: textFocus;
  animation-duration: .9s;
  animation-timing-function: ease-in;
}

.hero-description {
